import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Fade from 'react-reveal/Fade';
import theme from '@style/theme'

const HowSection = ({ title, desc, children}) => (
  <HowContainer>
        <Fade>
          <Title>{title}</Title>
          <p>{desc}</p>
        </Fade>
          {children}
  </HowContainer>
);

HowSection.propTypes = {
  children: PropTypes.element.isRequired,
};

const HowContainer = styled.div`
  padding: 20px 0;
  border-top: 1px solid ${theme.color.grayscale.S400};
  @media (max-width: ${theme.screen.md}) {
    margin: 30px 0;
    padding: 15px 0 0 0;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0px;
  }
  :first-of-type{
    margin: 0;
    padding-top: 0;
    border-top: none;
  }
  :last-of-type{
    margin: 0 0 5px 0;
    border-bottom: none;
  }
`

HowSection.propTypes = {
  children: PropTypes.element.isRequired,
};


const Title = styled.div`
  margin-bottom: 5px;
  ${theme.fontSize.regular};
  font-weight: bold;

`
export default HowSection;

